import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { UserFavouriteListComponent } from './user-favourite-list.component';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { FormsModule } from '@angular/forms';
import { UserFavouriteService } from 'src/app/service/user-favourite.service';

const routes: Routes = [
  {
    path: '',
    component: UserFavouriteListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../user-favourite-add/user-favourite-add.module').then(m => m.UserFavouriteAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../user-favourite-add/user-favourite-add.module').then(m => m.UserFavouriteAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    UserFavouriteListComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    PaginationModule,
    LoderModule,
    NgSelectModule,
    FormsModule
  ],
  providers: [
    UserFavouriteService
  ]
})

export class UserFavouriteListModule { }
