import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class UserFavouriteService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getUserFavouriteList(param): Observable<any> {
    return this.http.get(`${this.API_URL}/user-favourite`, { params: param });
  }

  addUserFavourite(data): Observable<any> {
    return this.http.post(`${this.API_URL}/user-favourite`, data);
  }

  editUserFavourite(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/user-favourite/${id}`, data);
  }

  deleteUserFavourite(id): Observable<any> {
    return this.http.delete(`${this.API_URL}/user-favourite/${id}`);
  }

  getUserFavouriteById(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/user-favourite/${id}`, { params: param });
  }
}